@import url('https://fonts.googleapis.com/css2?family=Azeret+Mono&family=Sigmar&display=swap');

* {
    padding: 0;
    margin:0;
}

.beer-container {
    display: grid;
    padding:3%;
    border:8px solid black;
    background-color: rgb(238, 221, 201);
    height:400px;
    width:240px;
    font-family: Azeret Mono;

    .beer-container__image {
        height:200px;
        margin:auto;
        padding-top:5px;
        padding-bottom:8px;
    }

    .beer-container__name {
        padding:10px;
        font-size: 26px;
        text-decoration: none;
        color:Black;
        font-weight: 600;
    }

    .beer-container__description {
        padding:10px;
        font-size: 20px;
    }
}
